// import css from '@emotion/css';
import styled from "@emotion/styled";
import React from "react";
import { useRecoilValue } from "recoil";
import baseKeyState from "../../../recoil/baseKeyState";
import keys, { FUTURE, regex } from "../keys";

export default ({ chord }: { chord: string }) => {
  const baseKey = useRecoilValue(baseKeyState);
  let result = "";
  if (chord) {
    let tempArray = keys;
    chord.match(regex)?.map((chord) => {
      let tempChord = chord;

      if (tempChord === "Bᵇ") {
        tempArray = FUTURE["Bᵇ"];
      } else if (tempChord === "Dᵇ") {
        tempArray = FUTURE["Dᵇ"];
      } else if (tempChord === "Eᵇ") {
        tempArray = FUTURE["Eᵇ"];
      } else if (tempChord === "Gᵇ") {
        tempArray = FUTURE["Gᵇ"];
      } else if (tempChord === "Aᵇ") {
        tempArray = FUTURE["Aᵇ"];
      } else {
        tempArray = keys;
      }
      if (tempArray.indexOf(tempChord) === -1) {
        result = result + tempChord.replace(/♭/g, "ᵇ");
      } else {
        result =
          result +
          tempArray[
            Math.abs(tempArray.indexOf(tempChord) + baseKey) % tempArray.length
          ].replace(/♭/g, "ᵇ");
      }
    });
  }
  return (
    <ChordCode
      dangerouslySetInnerHTML={{
        __html: result || "ㅤ<sup>ㅤ</sup>",
      }}
    />
  );
};

const ChordCode = styled.var`
  font-size: 15px;
  font-weight: 900;
  color: #1e1e1e;
  width: 100%;
  white-space: pre;
  /* white-space: nowrap; */

  @media screen and (max-width: 700px) {
    white-space: pre-wrap;
    font-weight: 400;
  }

  html[color-mode="dark"] & {
    color: #ffffff;
  }

  .print & {
    color: #1e1e1e !important;
  }

  @media screen and (max-width: 1300px) {
    font-size: 13px;
  }

  @media screen and (max-width: 1075px) {
    font-size: 12px;
  }

  @media screen and (max-width: 1040px) {
    font-size: 11px;
  }

  @media screen and (max-width: 685px) {
    font-size: 11px;
  }

  @media screen and (max-width: 600px) {
    font-size: 10px;
  }

  @media screen and (max-width: 585px) {
    transform: scale(0.7);
    transform-origin: left;
  }

  @media screen and (max-width: 575px) {
    transform: scale(0.55);
  }
`;
