import React from "react";
import css from "@emotion/css";
import styled from "@emotion/styled";

import ValueAdjuster from "./ValueAdjuster";
import PlayButton from "./PlayButton";
import useWindowSize from "../../../utils/useWindowSize";
import { useRecoilState } from "recoil";
import keys, { Flat, Minor } from "../keys";

// TODO: move the recoil objects under NotePlayer folder after refactoring
import baseKeyState from "../../../recoil/baseKeyState";
import speedState from "../../../recoil/speedState";
import chordSizeState from "../../../recoil/chordSizeState";
import isNotePlayerLoadingState from "../../../recoil/isNotePlayerLoadingState";

import SpeedIcon from "../../../assets/icons/speed.svg";
import KeyIcon from "../../../assets/icons/key.svg";
import ChordLyricsIcon from "../../../assets/icons/chord_lyrics.svg";
import CloseIcon from "../../../assets/icons/close.svg";

interface IControllerWrapper {
  isControllerHidden?: boolean;
}

interface IController extends IControllerWrapper {
  info: any;
  isPlaying?: boolean;
  onClickToggleController?: () => void;
  onClickPlayButton: () => void;
  hideProgress: boolean;
}

const Controller: React.FC<IController> = ({
  isPlaying = false,
  isControllerHidden = false,
  onClickToggleController,
  onClickPlayButton,
  hideProgress,
  info,
}) => {
  const [speed, setSpeed] = useRecoilState(speedState);
  const [baseKey, setBaseKey] = useRecoilState(baseKeyState);
  const [chordSize, setChordSize] = useRecoilState(chordSizeState);

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [isNotePlayerLoading, setIsNotePlayerLoading] = useRecoilState(
    isNotePlayerLoadingState
  );

  const { width: screenWidth = 1980 } = useWindowSize();
  const isSmallScreen = screenWidth <= 840;

  const setSpeedPlus = () =>
    setSpeed(Number(parseFloat((speed + 0.1).toString()).toPrecision(12)));

  const setSpeedMinus = () => {
    if (speed - 0.1 > 0) {
      setSpeed(Number(parseFloat((speed - 0.1).toString()).toPrecision(12)));
    }
  };

  const onClickNextBaseKey = () => {
    const nextIndex = baseKey + 1;
    setBaseKey(nextIndex);
  };

  const onClickPrevBaseKey = () => {
    const prevIndex = baseKey - 1;

    if (info.original_key.replace(/♭/g, "ᵇ").indexOf("ᵇ") >= 0) {
      setBaseKey(
        prevIndex < 0
          ? (Flat.length - Math.abs(prevIndex)) % Flat.length
          : prevIndex
      );
    } else if (info.original_key.indexOf("m") >= 0) {
      setBaseKey(
        prevIndex < 0
          ? (Flat.length - Math.abs(prevIndex)) % Flat.length
          : prevIndex
      );
    } else {
      setBaseKey(
        prevIndex < 0
          ? (keys.length - Math.abs(prevIndex)) % keys.length
          : prevIndex
      );
    }
  };

  const onClickPrevChordSize = () => {
    setIsNotePlayerLoading(true);
    if (chordSize + 0.5 <= 4) {
      setChordSize((prevSize) => prevSize + 0.5);
    }
    setTimeout(() => {
      setIsNotePlayerLoading(false);
    }, 500);
  };

  const onClickNextChordSize = () => {
    setIsNotePlayerLoading(true);
    if (chordSize - 0.5 >= 1) {
      setChordSize((prevSize) => prevSize - 0.5);
    }
    setTimeout(() => {
      setIsNotePlayerLoading(false);
    }, 500);
  };

  return (
    <ControllerWrapper isControllerHidden={isControllerHidden}>
      <ControllerContainer
        isControllerHidden={isControllerHidden}
        onClick={isControllerHidden ? onClickToggleController : undefined}
      >
        {isControllerHidden ? (
          <OpenIconWrapper>
            <CloseIcon />
          </OpenIconWrapper>
        ) : (
          <ControllerContent>
            <SmallScreenContainer>
              <PlayButtonWrapper>
                <PlayButton isPlaying={isPlaying} onClick={onClickPlayButton} />
                <CloseControllerButton onClick={onClickToggleController}>
                  <CloseIcon />
                </CloseControllerButton>
              </PlayButtonWrapper>
              <AdjusterList>
                {!hideProgress && (
                  <ValueAdjuster
                    isSmallScreen={isSmallScreen}
                    field="Speed"
                    value={`${speed}x`}
                    onClickPlus={() => setSpeedPlus()}
                    onClickMinus={() => setSpeedMinus()}
                    icon={<SpeedIcon />}
                  />
                )}
                <ValueAdjuster
                  isSmallScreen={isSmallScreen}
                  field="Key"
                  value={
                    info.original_key.replace(/♭/gi, "ᵇ").indexOf("ᵇ") >= 0
                      ? Flat[
                          (baseKey +
                            Flat.indexOf(
                              info.original_key
                                .replace(/♭/gi, "ᵇ")
                                .replace("#", "<sup>#</sup>")
                            )) %
                            Flat.length
                        ]
                      : info.original_key.indexOf("m") >= 0
                      ? Minor[
                          (baseKey +
                            Minor.indexOf(
                              info.original_key.replace("#", "<sup>#</sup>")
                            )) %
                            Minor.length
                        ]
                      : // keys.indexOf(
                        //     info.original_key.replace("#", "<sup>#</sup>")
                        //   ) < 0
                        // ? keys[baseKey % keys.length]
                        // :
                        keys[
                          (baseKey +
                            keys.indexOf(
                              info.original_key.replace("#", "<sup>#</sup>")
                            )) %
                            keys.length
                        ]
                  }
                  onClickPlus={onClickNextBaseKey}
                  onClickMinus={onClickPrevBaseKey}
                  icon={<KeyIcon />}
                />
                <ValueAdjuster
                  isSmallScreen={isSmallScreen}
                  field="Size"
                  value={`${chordSize}x`}
                  onClickPlus={onClickPrevChordSize}
                  onClickMinus={onClickNextChordSize}
                  icon={<ChordLyricsIcon />}
                  style={{ marginRight: 2 }}
                />
              </AdjusterList>
            </SmallScreenContainer>
          </ControllerContent>
        )}
      </ControllerContainer>
    </ControllerWrapper>
  );
};

export default Controller;

const ControllerWrapper = styled.div<IControllerWrapper>`
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  bottom: constant(safe-area-inset-bottom);
  bottom: env(safe-area-inset-bottom);
  display: flex;
  transition: 0.1s linear;

  @media screen and (max-width: 360px) {
    position: fixed;
    z-index: 999;
  }

  ${({ isControllerHidden = false }) =>
    isControllerHidden &&
    css`
      cursor: pointer;
    `};
`;

const ControllerContainer = styled.div<IControllerWrapper>`
  background-color: #0a84ff;
  border-radius: 14px;
  box-shadow: 0 2px 7px 0 rgba(139, 139, 139, 0.78);
  margin: 0 auto;

  ${({ isControllerHidden = false }) =>
    isControllerHidden &&
    css`
      width: 130px;
      display: flex;
      justify-content: center;
      padding-top: 21px;
      padding-bottom: 18px;
    `};

  @media screen and (max-width: 840px) {
    ${({ isControllerHidden = false }) =>
      isControllerHidden &&
      css`
        padding-top: 15px;
        padding-bottom: 14px;
        width: 100px;
      `};
  }
`;

const ControllerContent = styled.div`
  display: flex;
  padding: 23px 50px;
  padding-bottom: 20px;
  align-items: center;
  position: relative;

  @media screen and (max-width: 840px) {
    padding: 16px 15px;
    padding-top: 11px;
  }
`;

const SmallScreenContainer = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`;

const AdjusterList = styled.div`
  display: flex;
  align-items: flex-end;
  margin-left: 50px;

  @media screen and (max-width: 840px) {
    margin-left: 16px;
  }
`;

const PlayButtonWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
`;

const CloseControllerButton = styled.div`
  margin-top: 12px;
  cursor: pointer;

  & > svg {
    width: 23px;
    height: 11px;
  }

  @media screen and (max-width: 840px) {
    margin-top: 10px;
  }
`;

const OpenIconWrapper = styled.div`
  & > svg {
    transform: rotate(180deg);
    height: 11px;
    width: 23px;
  }
`;
