import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router';
import styled from '@emotion/styled';
import css from '@emotion/css';
import song from '../utils/song';

import {
  StyledServiceWrapper,
  BottomContainer,
  BottomContents,
} from '../components/SectionPage';
import NotePlayer from '../components/NotePlayer';
import { INoteData, initialNoteData } from '../components/NotePlayer/types';

import { api } from '../utils/api';

import { RecoilRoot } from 'recoil';
import useWindowSize from '../utils/useWindowSize';

const Notes: React.FC = () => {
  const { songIdx } = useParams<{ songIdx: string }>();
  const [noteData, setNoteData] = useState<INoteData>(initialNoteData);

  const { availHeight } = useWindowSize();
  const history = useHistory();

  useEffect(() => {
    const fetchData = async () => {
      try {
        const {
          data: { data },
        } = await api.get('/score/detail', { params: { id: songIdx } });
        setNoteData(data);
      } catch (err) { }
    };
    fetchData();
  }, [songIdx]);

  useEffect(() => {
    return () => {
      if (history.action === "POP") {
        const songId = song.getSelectedSong();
        if (!songId) return;

        const type = songId.split('-')[0];
        if (type == 'new')
          history.push('/recent');
        else if (type == 'rank')
          history.push('/chart');
      }
    };
  }, [history])

  return (
    <RecoilRoot>
      <NoteServiceWrapper
        isFooterRendered={false}
        containerStyles={{
          height: availHeight ? availHeight - 32 : undefined,
        }}
      >
        <StyledBottomContainer
          availHeight={availHeight ? availHeight - 64 : undefined}
        >
          <StyledBottomContents>
            {noteData.info && <NotePlayer data={noteData} />}
          </StyledBottomContents>
        </StyledBottomContainer>
      </NoteServiceWrapper>
    </RecoilRoot>
  );
};

export default Notes;

const NoteServiceWrapper = styled(StyledServiceWrapper)`
  @media screen and (max-width: 1300px) {
    width: 92%;
    margin: 0 auto;
  }

  @media screen and (max-width: 585px) {
    width: 94%;
  }
`;

interface IAvailHeight {
  availHeight?: number;
}

const StyledBottomContainer = styled(BottomContainer) <IAvailHeight>`
  display: flex;
  height: calc(100vh - 200px);

  ${({ availHeight }) =>
    availHeight &&
    css`
      height: ${availHeight - 200}px;
    `};

  @media screen and (max-width: 1300px) {
    padding: 0;
    margin-top: 32px;
  }

  @media screen and (max-width: 585px) {
    margin-top: 16px;
  }
`;

const StyledBottomContents = styled(BottomContents)`
  padding-top: 56px;
  padding-bottom: 0;
  max-width: 1400px;
  height: auto;

  @media screen and (max-width: 1300px) {
    width: 100%;
    padding: 0;
  }

  @media screen and (max-width: 500px) {
    margin-top: 0;
  }
`;
